div.post {
    background-color: #e2e2e2;
    border-style: solid;
    border-color: black;
    border-width: 1px;
    width: 100%;
    white-space: pre-wrap;
}
.SearchComponentDiv {
    padding-top: 22vh;
    width: 60%;
    margin: auto;
}
.ComposeComponentDiv {
    padding-top: 22vh;
    padding-bottom: 5vh;
    width: 70%;
    height: 100%;
    margin: auto;
}

.ComposeTextArea {
    height: 100%;
    margin-left: 1%;
}

textarea[name=composeTextArea] {
    margin-top: 2%;
    margin-bottom: 2%;
    width: 70%;
    resize: none;
}

label[name=composeLabel] {
    font-size: large;
}

input[name=composeInput] {
}

input[name=composeSubmitButton] {
    margin-top: 2%;
    font-size: large;
}

@media only screen and (max-aspect-ratio: 1/1) {
    .ComposeComponentDiv{
        width: 100%;
    }
}
.TopNavDiv {
    width: 100%;
    position: fixed;
    text-align: center;
    background-color: lightgray;
    opacity: 0.8;
    padding: 1%;
    z-index: 100;
    min-height: 15vh;
}

.h1{
    color: white;
}

ul.TopNavUL li {
    display: inline;
    text-align: center;
    padding-left: 3%;
    padding-top: 10px;
    padding-right:  3%;
}
.topButt{
    background-color: lightgray;
    color: black;
    border: none;
    position: relative;
    font: 700 1rem 'Charm', cursive;
    transition: 0.3s ease-in-out;
    padding: 10px 35px;
    text-align: center;
    cursor: pointer;
}
.topButt:hover{
    background-color: dimgray;
}

@media only screen and (max-aspect-ratio: 1/1) {
    .topButt {
        position: relative;
        padding: 0px 0px;
        text-align: center;
        cursor: pointer;
        font-size: x-small;
    }
    .TopNavDiv{
        width: 100%;
        position: fixed;
        text-align: center;
        background-color: lightgray;
        opacity: 0.8;
        padding: 1%;
        z-index: 100;
        min-height: 15vh;
    }
}
.Browsediv {
    padding-top: 22vh;
    width: 60%;
    margin: auto;
}

@media only screen and (max-aspect-ratio: 1/1) {
    .Browsediv {
        width: 100%;
    }
}
.ShowSingePostdiv {
    padding-top: 22vh;
    width: 60%;
    margin: auto;
}


.oneComment {
    margin-top: 1%;
    margin-bottom: 1%;
    margin-left: 5%;
    margin-right: 5%;
    border-style: solid;
    border-width: 1px;
}

.contentP{
    text-align: left;
    margin: 3%;
    font: 500 1.0rem "sans-serif";
}
.authorP, .titleP{
    font-size: larger;
}

.date{
    margin-top: 1%;
}

.comAuth{
    font-size: large;
}

.submitCommentButton {
    margin-top: 1%;
}

.likeButton {
    background-color: Transparent;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
    outline:none;
    margin-bottom: 1%;
}

.deleteCommentButton {
    background-color: Transparent;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
    outline:none;
    margin-bottom: 1%;
}

.ControlButtons {
    margin: 3%;
    font-size: large;
    background-color: Transparent;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
    outline:none;
}

@media only screen and (max-aspect-ratio: 1/1) {
    .ShowSingePostdiv{
        width: 100%;
        margin: auto;
    }
}
